<template>

  <div class="input-form2 col-md-12 mx-auto">
    <h4 class="mb-3"> </h4>
    <form class="validation-form" @submit.prevent>
        <div class="mb-3">
          <label for="name"></label>
          <input type="text" class="form-control" v-model="state.cpName" id="copname" placeholder="" required>
          <div class="invalid-feedback">
             .
          </div>
        </div>
        <div class="mb-3">
          <label for="email"></label>
          <input type="email" class="form-control" v-model="state.email" id="email" placeholder="you@example.com">
             .
        </div>
        <button @click="emailVali()" class="btn btn-outline-primary w-100">
          
        </button>
        <button @click="emailCheck()" class="btn btn-outline-primary w-100" v-if="(state.ch == 1)">
           
        </button>
        <EmailCheck v-if="(state.change == 1)"
        :keys="state.keys"
        :email="state.email" />
      <div class="mb-3">
        <label for="copcode"></label>
        <input type="copcode" class="form-control" v-model="state.cpCode" id="copcode" placeholder="" required>
        <div class="invalid-feedback">
           .
        </div>
      </div>

      <div class="mb-3">
        <label for="password"></label>
        <input type="password" class="form-control" v-model="state.cpPw" id="coppassword">
        <div class="invalid-feedback">
           .
        </div>
      </div>

      <div class="mb-3">
        <label for="repassword"></label>
        <input type="password" class="form-control" v-model="state.reCpPw" id="recoppassword">
        <div class="invalid-feedback">
           .
        </div>
      </div>

      <div class="mb-3">
        <label for="phone"></label>
        <input type="text" class="form-control" v-model="state.cpPhone" id="copphone" placeholder="010-0000-0000" required>
        <div class="invalid-feedback">
           .
        </div>
      </div>

      <div class="mb-3">
        <label for="city"></label>
        <input type="text" class="form-control" v-model="state.city" id="city" placeholder=" " required>
        <div class="invalid-feedback">
           .
        </div>
      </div>

      <div class="mb-3">
        <label for="street"></label>
        <input type="text" class="form-control" v-model="state.street" id="street" placeholder="123" required>
        <div class="invalid-feedback">
            .
        </div>
      </div>

      <div class="mb-3">
        <label for="zipcode"></label>
        <input type="text" class="form-control" v-model="state.zipcode" id="zipcode" placeholder="12345" required>
        <div class="invalid-feedback">
           .
        </div>
      </div>

    
      <hr class="mb-4">
      <div class="custom-control custom-checkbox">
        <input type="checkbox" class="custom-control-input" v-model="state.aggrement" id="aggrement" required>
        <label class="custom-control-label" for="aggrement">    .</label>
      </div>
      <div class="mb-4"></div>
      <button class="btn btn-primary btn-lg btn-block" type="submit" @click="joinHandler" > </button>
        <tr><td><a href="http://localhost:9090/oauth2/authorization/google">Google</a></td></tr> 
    </form>
  </div>

  </template>
  
  <script>
  import {reactive} from '@vue/reactivity'
  import axios from 'axios'
  import router from '@/router'
  import EmailCheck from './EmailCheck.vue'
  export default {
    name:'ToJoin',
    components: { EmailCheck },
  setup(){
    const state = reactive({
      email : '',
      id          : '',
      cpCode       : '',
      cpPw    : '',
      reCpPw  : '', 
      cpName        : '',
      cpPhone       : '',
      city        : '',
      street      : '',
      zipcode     : '',
      change: 0,
    })

    const emailCheck = () => {

    state.change = 1;
    if (state.email === "") {
      alert(" ");
      state.email.value.focus();
      return false;
    }
    const url = "/api/passFind";
    const headers = {
      "Content-Type": "application/json",
    };
    console.log("---------------------");
    console.log(state.email);
    const body = { email: state.email };
    axios.get(url, body, { headers }).then(function (res) {
      console.log(res);
      if (res.data.email === state.email) {
        alert("  .")
        state.change = 0;
      } else if (res.data != "" || res.data.email != state.email) {
        console.log(typeof (res.data));
        alert("   .");
        axios.post("/api/join/emailCheck", body, { headers }).then(function (res) {
          console.log("---------------------------");
          console.log(res);
          console.log(res.data.key);
          state.key = res.data.key;
          state.emailVal = res.data.email;
        });
      }  else {
        alert("  ");
      }
    });

    }

    const emailVali = async () => {
      const url = "/api/emailVali"
      const headers = { "Content-Type": "application/json; charset=utf-8;" }
      console.log(state.email)
      console.log("===============")
      const body = { email: state.email };
      console.log(state.email);
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.data.validate === true) {
          alert("  .")
          return false;
        } else if (state.email === '') {
          alert(' ');
          state.email.value.focus();
          return false;
        } else if (!(state.email.includes("@") && state.email.includes("."))) {
          alert('   .'); 
          state.email.value.focus(); return false;
        } else (res.data.validate === false)
          alert("  .")
          state.ch = 1
      })
    }

    const joinHandler = async() => {
      console.log("asas")
      const url = './api/companyRegister'
      const headers = {
        "Content-Type" : "application/json",
      }
      const body = {
        email : state.email,
        id : state.id,
        crn : state.cpCode,
        password   : state.cpPw,
        name : state.cpName,
        phone : state.cpPhone,
        city : state. city,
        street : state.street,
        zipcode : state.zipcode,
        nickname: state.cpName,
      }
      console.log(body)
      if (state.cpName === '') {
      alert(' '); 
      state.nickname.value.focus(); return false;
      } else if (state.email === '') {
      alert(' '); 
      state.email.value.focus(); return false;
      } else if (!(state.email.includes("@") && state.email.includes("."))) {
      alert('   .'); 
      state.email.value.focus(); return false;
      } else if (state.copcode === '') {
      alert(' '); 
      state.copcode.value.focus(); return false;
      } else if (state.password === '') {
      alert(' '); 
      state.password.value.focus(); return false;
      } else if (state.repassword === '') {
      alert(' '); 
      state.repassword.value.focus(); return false;
      } else if (state.name === '') {
      alert(' '); 
      state.nickname.value.focus(); return false;
      } else if (state.nickname === '') {
      alert(' '); 
      state.name.value.focus(); return false;
      } else if (state.phone === '') {
      alert(' '); 
      state.phone.value.focus(); return false;
      } else if (state.city === '') {
      alert(' '); 
      state.city.value.focus(); return false;
      } else if (state.street === '') {
      alert(' '); 
      state.street.value.focus(); return false;
      } else if (state.zipcode === '') {
      alert(' '); 
      state.zipcode.value.focus(); return false;
      } else if (state.password != state.repassword) {
      alert('  .  ');
      state.password.value.value = ''; 
      state.repassword.value.value = '';
      state.password.value.focus(); return false;
      }
      const response = await axios.post(url, body, {headers})
      if(response.status === 200){
        alert(' .');
      } else {
        alert(' .')
      }
      router.push({name: "Login"});
    }
    return {joinHandler,state,emailCheck,emailVali}
  }
  }
  </script>
  
  <style scoped>
  .container2{
  height: 100vh;
  }
  .input-form2 {
        text-align: center;
        padding: 32px;
  
        /* margin-top: 45px; */
  
        background: #fff;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        border-radius: 10px;
      }
  
  #hero{
    overflow: scroll;
  
    max-height: 1100px;
  }
  
  #hero::before{
    height: 1100px;
  }
  
  </style>